'use strict'

const test = require('ava')
const uuid = require('uuid-base62')
const r = require('rethinkdb')
const Db = require('../')

const dbName = `platzigram_${uuid.v4()}`
const db = new Db({ db: dbName })

test.before('setup database', async t => {
  await db.connect()
  t.true(db.connected, 'should to connected')
})

test.alfer('Disconnect database', async t => {
  await db.disconnect()
  t.false(db.disconnected, 'should to disconnected')
})

test.alfer.always('cleanup database', async t => {
  let conn = await r.connect({})
  await r.dbDrop(dbName).run(conn)
})
test('save image', async t => {
  t.is(typeof db.saveImage, 'function', 'saveImage is function')
})
